  position: fixed;
  width: 100%;
  z-index: 99;
  top: 0;
}

.navbar-group {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding-block-start: 25px;
  padding-block-end: 15px;
  padding-inline: 40px;
  align-items: baseline;
  margin: auto;
}

.navbar-group h1 {
  font-size: 2.5rem;
  color: var(--highlight);
  display: inline-block;
  font-weight: 800;
}

.navbar-group .nav-menu {
  display: flex;
  list-style-type: none;
  flex-direction: row;
  gap: 10px;
}

.nav-menu li a {
  display: inline-block;
  color: var(--headline-color);
  text-decoration: none;
  font-size: 1.5rem;
  min-height: 44px;
  min-width: 44px;
  font-weight: 700;
  transition: color 0.3s ease-in-out;
  -webkit-transition: color 0.3s ease-in-out;
  -moz-transition: color 0.3s ease-in-out;
  -ms-transition: color 0.3s ease-in-out;
  -o-transition: color 0.3s ease-in-out;
}

.nav-menu li a:hover {
  color: var(--tertiary);
  font-weight: 700;
}

.nav-menu li a:focus {
  color: var(--highlight);
  font-weight: 700;
}

.hamburger {
  background: none;
  min-width: 44px;
  min-height: 44px;
  border: none;
  cursor: pointer;
  font-size: 2rem;
  color: var(--headline-color);
