}

/* Button Styling */
button {
  display: inline-block;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 1rem;
  color: #fff;
  background-color: var(--primary-color);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: var(--primary-color-hover);
}

/* Favorites Section Styling */

.favorites-section {
  max-width: 1024px;
  margin: 0 auto;
  text-align: center;
  margin-block-start: 140px;
  padding-block-start: 25px;
  padding-block-end: 15px;
  padding-inline: 20px;
}

.favorites-section h1 {
  font-size: 2em;
  margin-bottom: 20px;
  color: #333;
}

