}

.footer {
  background-color: var(--background-color);
  color: var(--paragraph-color);
  padding-inline: 20px;
  padding-block: 30px;
  margin-top: 40px;
  text-align: center;
  font-size: 1rem;
  font-weight: 700;
}

.hamburger:focus,
.nav-menu li a:focus,
.hamburger-menu .hamburger-items li a:focus {
  outline: 2px solid var(--headline-color);
}

[tabindex]:focus {
  outline: 3px solid var(--button-color); /* Highlight focus */
}

/* like button */

.like {
  width: 55px;
  height: 55px;
  display: flex;
